.header {
    display: flex;
    justify-content: space-between;
    margin:  10px;
    border: solid 1px black;
}

.nav-items > ul {
    list-style: none;
    display: flex;
}

.nav-items > ul > li {
    padding: 10px;
}

.logo {
    width: 110px;
}

.card {
    width: 220px;
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
    height: 420px;
}

.card > img {
    width: 100%;
}

.restraurant-list {
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card {
    width: 220px;
    height: 300px;
    background-color: lightgray;
    margin: 10px;
    padding: 10px;
}

.menu {
    display: flex;
}

/* Form */

.section-heading {
    color: #91ac41;
    font-size: 24px;
    font-family: 'Caveat', cursive;
    margin: 0;
    margin-top: 35px;
  }
  
  .section-description {
    font-size: 20px;
    font-weight: 400;
    color: #8b8b99;
    letter-spacing: 0.5px;
  }

  form {
    display: block;
    margin-top: 0em;
  }
  
  .input-box {
    width: 100%;
    padding: 16px;
    font-size: 16px;
    color: #8b8b99;
    border-radius: 6px;
    border: 1px solid #e5e6ec;
    margin-bottom: 16px;
    box-sizing: border-box;
  }

  #btn-submit {
    font-size: 14px;
    text-transform: uppercase;
    cursor: pointer;
  }

  /* Footer */

  .footer {
    background-color: #232323;
    color: white;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-align: center;
    padding: 30px 0px;
}

/* Video */

.video-section {
    background: white;
    margin-top: 0;
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    text-align: center;
  }

  .video-section h3 {
    color: #8b8b99;
    font-size: 44px;
    font-weight: 400;
  }